<template>
  <el-select
    :value="value"
    :size="size"
    :multiple="multiple"
    :disabled="disabled"
    :value-key="valueKey"
    :clearable="clearable"
    :filterable="filterable"
    :placeholder="placeholder"
    :allowCreate="allowCreate"
    :multipleLimit="multipleLimit"
    @change="selectChange"
  >
    <el-option
      label=""
      key="select-all"
      :value="
        valueKey
          ? { [selfProps.value]: empty, [selfProps.label]: '' }
          : empty
      "
      v-if="multiple && selfData.length > showTotal"
    ></el-option>
    <el-option
      v-for="item in selfData"
      :key="item[selfProps.value]"
      :label="item[selfProps.label]"
      :value="valueKey ? item : item[selfProps.value]"
    >
    </el-option>
  </el-select>
</template>

<script>
/**
 * auth:weilan
 * github: https://github.com/hql7
 * description: el-select
 * props:
 *    data->
 *    props-> label:"label", value: "value"
 *    showTotal-> ,multiple
 *    value-> v-model
 *    others->https://element.eleme.cn/#/zh-CN/component/select
 * emit:
 */
export default {
  name: "wlVueSelect",
  data() {
    return {
      // value: "", // 
      empty: "00000000-0000-0000-0000-000000000000" // id
    };
  },
  model: {
    prop: "value", //v-model
    event: "change"
  },
  props: {
    // 
    data: {
      type: Array,
      default: () => {
        return [];
      }
    },
    //  label:"label", value: "value"
    props: Object,
    // ,
    showTotal: {
      type: Number,
      default: 1
    },
    // (v-model)
    defaultSelect: {
      type: Boolean,
      default: false
    },
    // v-model
    value: [String, Array, Object],
    // value-key  value 
    valueKey: [String],
    // 
    multiple: {
      type: Boolean,
      default: false
    },
    disabled: {
      // 
      type: Boolean,
      default: false
    },
    size: String, // medium/small/mini
    clearable: {
      // 
      type: Boolean,
      default: false
    },
    multipleLimit: {
      //  0 
      type: Number,
      default: 0
    },
    placeholder: {
      // 
      type: String,
      default: ""
    },
    filterable: {
      // 
      type: Boolean,
      default: false
    },
    allowCreate: {
      //  filterable 
      type: Boolean,
      default: false
    }
  },
  methods: {
    selectChange(val) {
      if (!this.multiple || val.length === 0) {
        this.$emit("change", val);
        return;
      }
      // 
      let _data = [];
      let _all_item_index = this.valueKey
        ? val.findIndex(item => item[this.selfProps.value] === this.empty)
        : val.findIndex(item => item === this.empty);
      if (_all_item_index === -1 && val.length === this.selfData.length) {
        _data = this.valueKey
          ? [
              {
                [this.selfProps.value]: this.empty,
                [this.selfProps.label]: ""
              }
            ]
          : [this.empty];
      } else {
        let arr_length_index = val.length - 1;
        if (_all_item_index === arr_length_index) {
          _data = [val[arr_length_index]];
        } else {
          _data = this.valueKey
            ? val.filter(item => item[this.selfProps.value] !== this.empty)
            : val.filter(item => item !== this.empty);
        }
      }
      /* let _all_item = this.valueKey
        ? val.find(item => item[this.selfProps.value] === this.empty)
        : val.find(item => item === this.empty);
      let _data = _all_item
        ? [_all_item]
        : val.length !== this.data.length
        ? val
        : this.valueKey
        ? [
            {
              [this.selfProps.value]: this.empty,
              [this.selfProps.label]: ""
            }
          ]
        : [this.empty]; */
      this.$emit("change", _data);
    }
  },
  computed: {
    // 
    selfData() {
      return this.data;
    },
    // props
    selfProps() {
      return { label: "label", value: "value", ...this.props };
    }
  },
  watch: {
    // 
    selfData(val) {
      if (!this.defaultSelect || val.length === 0) return;
      if (!this.multiple) {
        this.$emit("change", val[0]);
      } else {
        let select_item =
          val.length > this.showTotal
            ? {
                [this.selfProps.value]: this.empty,
                [this.selfProps.label]: ""
              }
            : val[0];
        let _data = !this.valueKey
          ? select_item[this.selfProps.value]
          : select_item;
        this.$emit("change", [_data]);
      }
    }
  }
};
</script>
